import React, { useEffect, useState, useCallback, useContext } from 'react';
import { StaticContext } from 'react-router';
import { Route, Redirect, RouteComponentProps } from 'react-router-dom';
import FacebookLogin from 'react-facebook-login';

import styles from './css/user.module.css';
import contactFields from '../resources/contact';
import { UserSwitcher } from '../components/Switcher';
import { GET, POST } from '../utils/requests';
import { ProfileData, Team } from '../interfaces';
import { FaAngleRight, FaCheckCircle, FaSyncAlt, FaLink, FaChess, FaUserCircle, FaCogs, FaUsers } from 'react-icons/fa';
import { getChessComProfile } from '../chess/ChessCom';
import { AlertContext } from '../Contexts';
import { Lichess, ChessCom, Facebook } from './callbacks';

export default function Info(props: RouteComponentProps<any, StaticContext, any> & {
	profile: Team & ProfileData
	updateProfile: () => void
	endSession: () => Promise<void>
}) {
	
	const href = Lichess.useHref({ currentPath: props.location.state.currentPath });
	const { handleSync, setWindow } = Lichess.useSync({ updateProfile: props.updateProfile });
	const handleFacebook = Facebook.useFacebook({ updateProfile: props.updateProfile });
	const handleChessComLink = ChessCom.useLink({ updateProfile: props.updateProfile });
	const handleChessComVerify = ChessCom.useVerify({ profile: props.profile, updateProfile: props.updateProfile });

	const [redirect, setRedirect] = useState(false);
	if (props.location.search.startsWith('?user=')) window.close();
	if (redirect) return <Redirect to='/' />;

	return (
		<div className={styles.container}>
			<div className={styles.title}>
				<div className={['container', styles.info].join(' ')}>
					<h1><span className='bold'>{props.profile.username}</span></h1>						
					<div className={styles.details}>
						<div>Email: {props.profile.email}</div>
						<div>Account creation: {new Date(props.profile.createdAt).toString().slice(0, 15)}</div>{/*}
						<div>Tournaments: {tournaments.length}</div>
						<div>Public tournaments: {tournaments.filter(t => t.setting && t.setting.public).length}</div>*/}
					</div>
					<div className={styles.buttons}>
						<div className={styles.row}>								
							<a href='https://beta.scorchapp.co.uk' rel='noopener noreferrer' target='_blank' className={['button', 'link', styles.new].join(' ')}>
								Create New Tournament
								<FaAngleRight />
							</a>							
							<div className={['button', 'link', styles.logout].join(' ')} onClick={() => {
								props.endSession().then(() => setRedirect(true));
							}}>
								Log out
								<FaAngleRight />
							</div>
						</div>
						<div className={styles.row}>
							<a
								id='lichess'
								href={props.profile.lichess ? (contactFields.lichess.link as string).replace('|', props.profile.lichess.username) : href}
								className={['button', 'link', styles.account].join(' ')}
								rel='noopener noreferrer'
								target='_blank'
							>
								<img src={contactFields.lichess.icon[1] as string} alt='lichess' />
								{props.profile.lichess?.username || 'Link Lichess account'}
								{props.profile.lichess?.verified ? <FaCheckCircle className='verified' /> : null}
								{!props.profile.lichess ? <FaAngleRight /> : null }
							</a>
							{props.profile.lichess ?
								props.profile.lichess?.verified ?
									<div id='lichess' className={['button', 'link', styles.sync].join(' ')} onClick={() => handleSync('lichess')}>
										<FaSyncAlt />
									</div> :
									<div id='lichess' className={['button', 'link', styles.sync].join(' ')} onClick={() => setWindow(window.open(href))}>
										<FaLink />
									</div> :
								null
							}
						</div>
						<div className={styles.row}>
							{props.profile.chessCom?.username ?
								<a
									id='chessCom'
									href={(contactFields.chessCom.idLink as string).replace('|', props.profile.chessCom.username)}
									className={['button', 'link', styles.account].join(' ')}
									rel='noopener noreferrer'
									target='_blank'
								>
									<img src={contactFields.chessCom.icon[1] as string} alt='lichess' />
									{props.profile.chessCom.username}
									{props.profile.chessCom.verified ? <FaCheckCircle className='verified' /> : null}
								</a> :
								<div
									id='chessCom'
									className={['button', 'link', styles.account].join(' ')}
									onClick={handleChessComLink}
								>
									<img src={contactFields.chessCom.icon[1] as string} alt='chessCom' />
									Link Chess.Com account
									<FaAngleRight />
								</div>
							}
							
							{props.profile.chessCom && props.profile.chessCom.username ?
								props.profile.chessCom.verified ?
									<div id='chessCom' className={['button', 'link', styles.sync].join(' ')} onClick={() => handleSync('chessCom')}>
										<FaSyncAlt />
									</div> :
									<div id='chessCom' className={['button', 'link', styles.sync].join(' ')} onClick={handleChessComVerify}>
										<FaLink /> Verify
									</div> :
								null
							}
						</div>					
						<div className={styles.row}>
							{props.profile.facebook ?
								<a
									id='facebook'
									href={props.profile.facebook ? (contactFields.facebook.idLink as string).replace('|', props.profile.facebook.id as string) : href}
									className={['button', 'link', styles.account].join(' ')}
									rel='noopener noreferrer'
									target='_blank'
								>
									{contactFields.facebook.icon[1]}
									{props.profile.facebook.name}
									<FaCheckCircle className='verified' />
								</a> :
								<FacebookLogin
									appId='3423958477620478'
									fields='name,email'
									scope='public_profile'
									callback={handleFacebook}
								/>
							}
							
						</div>
					</div>
					
					<Route exact path={`/@/${props.profile.username}/:section`} render={(rProps) => {
						return <UserSwitcher
							section={rProps.match.params.section}
							loaded
							sections={{
								tournaments: {
									link: `/@/${props.profile.username}/tournaments`,
									icon: <FaChess />
								},
								teams: {
									link: `/@/${props.profile.username}/teams`,
									icon: <FaUsers />
								},
								accounts: {
									link: `/@/${props.profile.username}/accounts`,
									icon: <FaUserCircle />
								},
								settings: {
									link: `/@/${props.profile.username}/settings`,
									icon: <FaCogs />
								}
							}}
						/>
					}} />
					
				</div>
			</div>
		</div>
	)
}